.check-box{
    float: left;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    height: 100%;
    padding: 0 .1rem;
}
.input_check{
    display: none;
}
.input_check + label {
    width: 20px;
    height: 20px;
}
.input_check:checked + label:before {
    top: 4px;
    left: 4px;
    width: 20px;
    height: 20px;
}
.input_check+label {
    background-color: #FFF;
    border: 1px solid #ccc;
    padding: 9px;
    border-radius: 1000px;
    display: inline-block;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.input_check:checked+label {
    background-color: #ECF2F7;
    border: 1px solid #F43530;
    color: #243441
}
.input_check:checked+label:before {
    content: ' ';
    border-radius: 100px;
    position: absolute;
    background: #F43530;
    display: none;
    top: 3px;
    left: 3px;
    width: 12px;
    height: 12px
}
.input_check:checked+label:before {
    content: ' ';
    display: block
}
